import { Component, OnInit } from '@angular/core';

import { ChartModule } from 'angular2-highcharts';
import * as highcharts  from 'highcharts';
import * as $ from 'jquery';
@Component({
  selector: 'app-hcharts',
  templateUrl: './hcharts.component.html',
  styleUrls: ['./hcharts.component.css'],
  providers: [ ChartModule.forRoot(highcharts).providers]
})
export class HchartsComponent implements OnInit {

  options:Object;
  constructor() {
    this.options = {
      chart: {
        plotBackgroundColor: null,
        plotBorderWidth: null,
        plotShadow: false,
        type: 'pie',
        width: $(document).width(),
        height: $(document).width()
      },
      title: {
        text: 'Browser market shares at a specific website, 2014'
      },
      tooltip: {
        pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
      },
      plotOptions: {
        pie: {
          allowPointSelect: true,
          cursor: 'pointer',
          dataLabels: {
            enabled: true,
            format: '<b>{point.name}</b>: {point.percentage:.1f} %'
          }
        }
      },
      series: [{
        name: 'Brands',
        data: [
          { name: 'Microsoft Internet Explorer', y: 56.33 },
          { name: 'Chrome', y: 24.03 },
          { name: 'Firefox', y: 10.38 },
          { name: 'Safari', y: 4.77 },
          { name: 'Opera', y: 0.91 },
          { name: 'Proprietary or Undetectable', y: 0.2 }
        ]
      }]
    }
  }

  ngOnInit() {
  }

}



