import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavComponent } from './components/nav/nav.component';
import { ContentComponent } from './components/content/content.component';
import { FooterComponent } from './components/footer/footer.component';
import { SwiperComponent } from './components/swiper/swiper.component';
import { ServiceService } from './services/service.service';
import { GitUserComponent } from './components/git-user/git-user.component';
import { GitUserListComponent } from './components/git-user-list/git-user-list.component';
import { SguserComponent } from './components/sguser/sguser.component';
import { UserLoginComponent } from './user/user-login/user-login.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ForgetPwdComponent } from './user/forget-pwd/forget-pwd.component';
import { FormsModule } from '@angular/forms';
import { ForgetPwdService } from './user/forget-pwd/forget-pwd-service';
import { ListComponent } from './list/list.component';
import {ChartModule} from "angular2-highcharts";
import { HchartsComponent } from './hcharts/hcharts.component';
import { EchartComponent } from './echart/echart.component';
import {NgxEchartsModule} from 'ngx-echarts';

@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    ContentComponent,
    FooterComponent,
    SwiperComponent,
    GitUserComponent,
    GitUserListComponent,
    SguserComponent,
    UserLoginComponent,
    ForgetPwdComponent,
    ListComponent,
    HchartsComponent,
    EchartComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    HttpModule,
    FormsModule,
    ChartModule,
    NgxEchartsModule
  ],
  providers: [ServiceService,ForgetPwdService],
  bootstrap: [AppComponent]
})
export class AppModule { }



