import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContentComponent } from './components/content/content.component';
import { GitUserListComponent } from './components/git-user-list/git-user-list.component';
import { SguserComponent } from './components/sguser/sguser.component';
import { UserLoginComponent } from './user/user-login/user-login.component';
import { ForgetPwdComponent } from './user/forget-pwd/forget-pwd.component';
import { ListComponent } from './list/list.component';
import { HchartsComponent } from './hcharts/hcharts.component';
import { EchartComponent } from './echart/echart.component';
const routes: Routes = [
  {
    path: '',component:ContentComponent
  },
  {
    path: 'gitlist',component:GitUserListComponent
  },
  {
    path: 'gitlist/:login',component:SguserComponent
  },
  {
    path: 'login',component:UserLoginComponent
  },
  {
    path: 'list',component:ListComponent
  },
  {
    path: 'forgetpwd',component:ForgetPwdComponent
  },
  {
    path: 'highcharts',component:HchartsComponent
  },
  {
    path: 'echarts',component:EchartComponent
  },
  {
    path:'user',
    loadChildren:'./user/user.module#UserModule'
  },
  {
    path: '**',component:ContentComponent,redirectTo:""
  }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



