import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ServiceService } from '../../services/service.service';
@Component({
  selector: 'app-sguser',
  templateUrl: './sguser.component.html',
  styleUrls: ['./sguser.component.css']
})
export class SguserComponent implements OnInit {
  login:string;
  user:object = {};
  constructor(private route:ActivatedRoute, private router:Router, public service:ServiceService) { 
  	this.route.params.subscribe((params:Params)=>{
  		this.login = params.login;
  	})
  }

  ngOnInit() {
  	this.service.getSingleUser(this.login).subscribe((gituser) => {
  		this.user = gituser.json()
  	})
  }


}



